"use client";
import React from "react";
import { useDesignerStore } from "../state/store";
import type { ComponentNode, FormSchema } from "@shadcn-builder/renderer";

function updateNode(schema: FormSchema, id: string, updater: (n: ComponentNode) => ComponentNode): FormSchema {
  return {
    ...schema,
    components: schema.components.map((n) => (n.id === id ? updater(n) : n)),
  };
}

function Section({ title, children }: { title: string; children: React.ReactNode }) {
  return (
    <div className="mb-4">
      <div className="text-xs uppercase tracking-wide text-slate-500 mb-2">{title}</div>
      <div className="space-y-2">{children}</div>
    </div>
  );
}

export function RightPanel() {
  const schema = useDesignerStore((s) => s.schema);
  const setSchema = useDesignerStore((s) => s.setSchema);
  const selectedId = useDesignerStore((s) => s.selectedId);

  const selected = schema.components.find((n) => n.id === selectedId) || null;

  if (!selected) return <div className="text-sm text-slate-500">未选择组件</div>;

  const setProp = (path: "properties.text" | "attributes.placeholder" | "properties.className", value: string) => {
    setSchema(
      updateNode(schema, selected.id, (n) => {
        if (path === "properties.text") {
          return { ...n, properties: { ...n.properties, text: value } };
        }
        if (path === "properties.className") {
          return { ...n, properties: { ...n.properties, className: value } };
        }
        if (path === "attributes.placeholder") {
          return { ...n, attributes: { ...(n.attributes || {}), placeholder: value } } as ComponentNode;
        }
        return n;
      })
    );
  };

  return (
    <div>
      <Section title="Label">
        {selected.type === "text" && (
          <label className="block text-sm">
            <span className="text-slate-600">文本</span>
            <input className="mt-1 border rounded px-2 py-1 w-full" value={(selected as any).properties?.text || ""} onChange={(e) => setProp("properties.text", e.target.value)} />
          </label>
        )}
        {selected.type === "input" && (
          <label className="block text-sm">
            <span className="text-slate-600">占位</span>
            <input className="mt-1 border rounded px-2 py-1 w-full" value={(selected as any).attributes?.placeholder || ""} onChange={(e) => setProp("attributes.placeholder", e.target.value)} />
          </label>
        )}
      </Section>
      <Section title="Grid">
        <label className="block text-sm">
          <span className="text-slate-600">className</span>
          <input className="mt-1 border rounded px-2 py-1 w-full" value={(selected as any).properties?.className || ""} onChange={(e) => setProp("properties.className", e.target.value)} />
        </label>
      </Section>
    </div>
  );
}
