"use client";
import React from "react";
import { useDesignerStore } from "../state/store";

const viewportLabels: Record<"sm"|"md"|"lg", string> = { sm: "Mobile", md: "Tablet", lg: "Desktop" };

export function ViewportSwitch() {
  const viewport = useDesignerStore((s) => s.viewport);
  const setSchema = useDesignerStore((s) => s.setSchema); // no viewport setter in store; we will update store via set to keep single source
  const state = useDesignerStore();
  const setViewport = (v: "sm"|"md"|"lg") => {
    // 简化：直接覆写 store 的 viewport 字段
    // @ts-expect-error mutate store key
    state.viewport = v;
  };
  return (
    <div className="flex items-center gap-1 border rounded px-1 py-0.5">
      {(["sm","md","lg"] as const).map((v) => (
        <button key={v} onClick={() => setViewport(v)} className={`text-xs px-2 py-1 rounded ${viewport===v?"bg-slate-900 text-white":"hover:bg-slate-100"}`}>{viewportLabels[v]}</button>
      ))}
    </div>
  );
}

export function Header({ onToggleJson, jsonVisible }: { onToggleJson?: () => void; jsonVisible?: boolean }) {
  return (
    <div className="h-14 border-b flex items-center gap-3 px-4 shrink-0 bg-white/80 backdrop-blur supports-[backdrop-filter]:bg-white/60">
      <div className="font-medium">Form Builder</div>
      <div className="ml-auto flex items-center gap-2">
        <ViewportSwitch />
        <button onClick={onToggleJson} className={`text-xs border rounded px-2 py-1 ${jsonVisible?"bg-slate-900 text-white":"hover:bg-slate-100"}`}>{jsonVisible?"Hide JSON":"Show JSON"}</button>
      </div>
    </div>
  );
}
