"use client";
import React from "react";
import { useDesignerStore } from "../state/store";
import type { FormSchema, ComponentNode } from "@shadcn-builder/renderer";

function generateId(prefix: string, schema: FormSchema) {
  let i = 1;
  while (schema.components.some((c) => c.id === `${prefix}-${i}`)) i++;
  return `${prefix}-${i}`;
}

function Group({ title, children }: { title: string; children: React.ReactNode }) {
  return (
    <div className="mb-4">
      <div className="text-xs uppercase tracking-wide text-slate-500 mb-2">{title}</div>
      <div className="grid grid-cols-2 gap-2">{children}</div>
    </div>
  );
}

export function LeftPanel() {
  const schema = useDesignerStore((s) => s.schema);
  const setSchema = useDesignerStore((s) => s.setSchema);

  const addComponent = (node: ComponentNode) => {
    const withId = { ...node, id: generateId(node.type, schema) };
    setSchema({ ...schema, components: [...schema.components, withId] });
  };

  return (
    <div>
      <Group title="Text">
        <button className="border px-2 py-1 rounded text-sm hover:bg-slate-50" onClick={() => addComponent({ id: "-", type: "text", properties: { text: "标题", className: "text-lg font-medium" } })}>标题</button>
        <button className="border px-2 py-1 rounded text-sm hover:bg-slate-50" onClick={() => addComponent({ id: "-", type: "text", properties: { text: "段落", className: "text-sm text-slate-600" } })}>段落</button>
      </Group>
      <Group title="Form">
        <button className="border px-2 py-1 rounded text-sm hover:bg-slate-50" onClick={() => addComponent({ id: "-", type: "input", attributes: { placeholder: "请输入" } })}>输入框</button>
        <button className="border px-2 py-1 rounded text-sm hover:bg-slate-50" onClick={() => addComponent({ id: "-", type: "text", properties: { text: "按钮", className: "inline-flex items-center justify-center rounded-md border px-3 py-1.5 text-sm bg-slate-900 text-white" } })}>按钮</button>
      </Group>
    </div>
  );
}
